    background-color: #ffffff;
}
.topbar-one .topbar_bg {
    position: absolute;
    left: 0;
    bottom: -16px;
    right: 0;
    height: 16px;
    z-index: 1;
}
.topbar-one .container {
    min-height: 126px;
    position: relative;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
}
.topbar-one__left {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
}
.topbar-one__left a {
    color: #878986;
    font-size: 18px;
    font-weight: 500;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}
.topbar-one__left a span {
